.list {
  li {
    position: relative;
    @include notlast {
      margin-bottom: 10px; }
    @include before {
      width: 10px;
      height: 5px;
      border-radius: 5px;
      background-color: $accent;
      display: inline-block;
      margin-right: 10px;
      vertical-align: middle; } } }
