.section {
  @include cover;
  position: relative;
  .container {
    position: relative;
    z-index: 1; }
  &__title {
    text-align: center;
    margin-bottom: 20px; }
  &__title-mb-lg {
    margin-bottom: 70px; }
  &__subttl {
    text-align: center;
    margin-bottom: 45px; }
  &__video {
    padding-top: 40%;
    position: relative;
    overflow: hidden;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      min-width: 100%;
      min-height: 100%; } }
  &__list-title {
    font-weight: 700;
    margin-bottom: 15px; }


  &--lg {
    padding-top: $padding-lg;
    padding-bottom: $padding-lg; }
  &--white {
    color: $white; } }

.section-icon {
  position: absolute;
  z-index: 0;
  pointer-events: none; }

.section-tv {
  @include cover;
  background-size: contain;
  position: relative;
  img {
    position: absolute;
    width: 83.5%;
    top: 7%;
    left: 8%; }
  &--left {
    max-width: 565px;
    padding-top: 420px; } }

.s-features {
  .section-icon {
    &.ic-comet {
      top: 10%;
      left: 3%; }
    &.ic-star2 {
      bottom: 15%;
      left: 5%; } } }

.s-play {
  padding-bottom: 160px;
  background-color: $brown-light;
  .wave {
    bottom: 0;
    width: 100%; }

  .section-icon {
    top: 50%;
    transform: translate(0, -50%);
    &.ic-comet {
      left: 5%; }
    &.ic-star4 {
      right: 5%; } }
  .cloud {
    position: absolute;
    top: 40%;
    right: 30%; } }

.s-gallery {

  .wave {
    height: 90px;
    min-width: calc(100% + 3px);
    &--top {
      top: -1px;
      right: 0;
      left: -2px;
      min-width: 100%; }
    &--bottom {
      bottom: 0;
      left: -1px;
      right: -1px;
      z-index: 1; } }
  &__inner {
    position: relative;
    overflow: hidden;
    max-height: 100vh;
    background-color: $purple-accent; }
  &__bg {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    // height: calc(100% - #{$padding-lg})
    height: 100%;
    @include cover; } }

.s-two-colored {
  background-color: $brown-light;
  overflow: hidden;
  position: relative;
  .wave {
    min-width: calc(100% + 5px);
    // height: 90px
    &--top {
      left: 0;
      top: 0;
      height: 80px; }
    &--bottom {
      bottom: -1px;
      height: 90px; } }
  &__title {
    margin-bottom: 15px; }
  &__top {
    @include cover;
    padding-top: 200px;
    padding-bottom: 90px; }
  &__bottom {
    @include cover;
    color: $white;
    background-color: $purple; }
  &__list-title {
    font-weight: 700;
    margin-bottom: 10px; }
  &__list-title-lg {
    margin-bottom: 30px; }
  &__list {
    margin-bottom: 25px; } }

.s-app {
  &__tv {
    // max-width: 565px
    padding-top: 380px;
    margin-bottom: 50px; }
  &__title {
    margin-bottom: 20px; }
  &__btns {
    @include flex(center, center); }
  &__btn {
    display: block;
    font-size: 0;
    margin-left: 5px;
    margin-right: 5px; } }

.s-order-sm {
  &__img {
    margin-left: -40%;
    pointer-events: none; }
  &__title {
    margin-bottom: 5px; }
  &__comet {
    top: $padding-md;
    left: 30px; }
  &__star-left-bottom {
    bottom: $padding-lg;
    left: 40px; }
  &__star-left-center {
    top: 50%;
    left: 10%; }
  &__star-right-top {
    right: 50px;
    top: $padding-md; } }

